import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import format from 'date-fns/format'

import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import EditIcon from '@material-ui/icons/Edit'
import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'

const styles = theme => ({
  root: {
    width: '100%',
    backgroundColor: theme.palette.background.paper
  }
})

const ShoppingCartList = (props) => {
  const {classes, shoppingCartList, onShowCartDetail} = props

  return (
    <div className={classes.root}>
      <List>
        {shoppingCartList.map((shoppingCart, index) => {
          const {title, discountPrice, cart, isLoading} = shoppingCart

          return (
            <ListItem
              key={index}
              role={undefined}
              divider={index !== (shoppingCartList.length - 1)}>
              <ListItemText>
                <Grid container direction='row' alignItems='center'>
                  <Grid item xs={8}>
                    <Typography variant='caption'>
                      {format(new Date(+cart.timestamp), 'YYYY/MM/DD HH:mm:ss')}
                    </Typography>
                    <Typography variant='subheading'>{title}</Typography>
                  </Grid>
                  <Grid item xs={4}>
                    <Typography color='error' variant='subheading'>$&nbsp;{discountPrice}</Typography>
                  </Grid>
                </Grid>
              </ListItemText>
              <ListItemSecondaryAction>
                <div>{isLoading}</div>
                <IconButton aria-label='Edit' onClick={() => onShowCartDetail(shoppingCart)}>
                  {isLoading ? <CircularProgress size={30} /> : <EditIcon />}
                </IconButton>
              </ListItemSecondaryAction>
            </ListItem>
          )
        })}
      </List>
    </div>
  )
}

export default withStyles(styles)(ShoppingCartList)



