import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import * as CONSTANT from '../../constant'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'
import Typography from '@material-ui/core/Typography'

import ShoppingCartList from './ShoppingCartList/ShoppingCartList'
import ProductModal from '../Common/ProductModal/ProductModal'

const styles = () => ({
  root: {},
  loader: {
    margin: '5rem',
    textAlign: 'center'
  },
  message: {
    margin: '5rem'
  }
})

class ShoppingCartContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  onShowCartDetail = (selectProduct) => {
    const {setSelectProduct, toggleModal, updateModalType} = this.props
    updateModalType(CONSTANT.DELETE)
    setSelectProduct(selectProduct)
    toggleModal()
  }

  onDeleteFromCart = (selectProduct) => {
    const {
      deleteShoppingCartItem,
      deleteLocalShoppingCartItem,
      toggleModal
    } = this.props

    deleteShoppingCartItem(selectProduct)
    deleteLocalShoppingCartItem({...selectProduct, isLoading: true})
    toggleModal()
  }

  render () {
    const {
      classes,
      selectProduct,
      shoppingCartList,
      selectedSpecList,
      isModalOpen,
      isDataLoaded,
      modalType,
      toggleModal,
      updateModalType
    } = this.props

    return (
      <div>
        <Grid container justify='center'>
          <Grid item xs={12}>
            {
              !isDataLoaded &&
              <div className={classes.loader}>
                <CircularProgress size={80} />
              </div>
            }
            {
              isDataLoaded && shoppingCartList.length === 0 &&
              <Typography variant='title' align='center' className={classes.message}>
                No product in your cart!
              </Typography>
            }
            <ShoppingCartList
              shoppingCartList={shoppingCartList}
              onShowCartDetail={this.onShowCartDetail}
              updateModalType={updateModalType} />
          </Grid>
        </Grid>
        {
          Object.keys(selectProduct).length > 0 &&
          <ProductModal
            selectProduct={selectProduct}
            isModalOpen={isModalOpen}
            modalType={modalType}
            toggleModal={toggleModal}
            selectedSpecList={selectedSpecList}
            onDeleteFromCart={this.onDeleteFromCart} />
        }
      </div>
    )
  }
}

export default withStyles(styles)(ShoppingCartContent)



