import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import ShoppingCartContent from '../component/ShoppingCart/ShoppingCartContent'

class ShoppingCart extends Component {
  componentDidMount () {
    const {shoppingCartList, setDataLoaded, getShoppingCartList} = this.props

    if (shoppingCartList.length === 0) {
      setDataLoaded(false)
      getShoppingCartList()
    }
  }

  render () {
    return (
      <main>
        <ShoppingCartContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    selectProduct: state.selectProduct,
    shoppingCartList: state.shoppingCartList,
    isDataLoaded: state.isDataLoaded,
    isModalOpen: state.isModalOpen,
    modalType: state.modalType
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    setSelectProduct: (selectProduct) => dispatch(actionCreators.setSelectProduct(selectProduct)),
    getShoppingCartList: () => dispatch(actionCreators.getShoppingCartList()),
    deleteShoppingCartItem: (product) => dispatch(actionCreators.deleteShoppingCartItem(product)),
    deleteLocalShoppingCartItem: (product) => dispatch(actionCreators.deleteLocalShoppingCartItem(product)),
    toggleModal: () => dispatch(actionCreators.toggleModal()),
    updateModalType: (modalType) => dispatch(actionCreators.updateModalType(modalType))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ShoppingCart)



